import React, { useEffect, useState } from 'react';

import Chat from './components/Chat/Chat';
import Join from './components/JoinPage/JoinPage';
import queryString from 'query-string';

import { BrowserRouter as Router, Redirect, Route } from "react-router-dom";

const App = () => {
  const [urlname, setName] = useState('');
  const [urlroom, setRoom] = useState('');
  useEffect(() => {
    const { name, room } = queryString.parse(window.location.href);
    setName(name);
    setRoom(room);
  })

  return (
    <Router>
      <Route path={`/join?name=${urlname}&room=${urlroom}`} component={Join} />
      <Route path="/chat" component={Chat} />
      <Redirect to="/join" />
    </Router>
  );
}
export default App;